.li {
    color: white;
    position: relative;
    text-decoration: none;
}

.li:hover {
    color: inherit;
    text-decoration: none;
}

.li::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.li:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}