#contactLine, #projectsLine, #aboutLine,
#line_grey, #line_green1, #line_green2 {
    animation: drawMap 4s ease;
    animation-fill-mode: forwards;
}

#contactGroup:hover, #aboutGroup:hover, #projectGroup:hover {
    opacity: 1 !important;
}

.animate > g > .li > #contactLine {
    stroke-dasharray: 787;
    stroke-dashoffset: 787;
}
.animate > g > .li > #aboutLine {
    stroke-dasharray: 904;
    stroke-dashoffset: 904;
}
.animate > g > .li > #projectsLine{
    stroke-dasharray: 1733;
    stroke-dashoffset: 1733;
}

.animate > g > #line_grey {
    stroke-dasharray: 506;
    stroke-dashoffset: 606;
}
.animate > g > #line_green1{
    stroke-dasharray: 463;
    stroke-dashoffset: 463;
}
.animate > g > #line_green2{
    stroke-dasharray: 574;
    stroke-dashoffset: 574;
}
@keyframes drawMap {
    to {
        stroke-dashoffset: 0;
    }
}

.animate > g > circle[id*="stop"] { 
    opacity: 0;
    animation: dropIn 6s forwards;
}

.activeGroup .active {
    animation: grow 2s ease;
    animation-fill-mode: forwards;
    transform: scale(1.0);
    transform-box: fill-box;
    transform-origin: center;
}

.activeGroup .inactive {
    animation: shrink 2s ease;
    animation-fill-mode: forwards;
    transform-box: fill-box;
    transform-origin: center;
}


@keyframes grow {
    to {
        transform: scale(1.5);
        fill: #020315;
        stroke: #D9D9D9;
    }
}

@keyframes shrink {
    to {
        transform: scale(1);
        stroke: #020315;
        fill: #D9D9D9;
    }
}

@keyframes dropIn {
    66% {
        opacity: 0;
        transform: translateY(-20%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
svg {
    pointer-events: all;
}
path {
    pointer-events: all;
}
.li {
    color: white;
    position: relative;
    text-decoration: none;
}

.li:hover {
    color: inherit;
    text-decoration: none;
}

.li::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.li:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

#proj::before {
    background-color: #FCFA21!important;
}
#about::before {
    background-color: #00D7C7!important;
}
#cont::before {
    background-color: #FC0000!important;
}

