        align-items: start;
    }
    .navigation {
        flex-direction: column-reverse;
    }
    .container-fluid {
        justify-content: center;
        overflow: scroll;
    }
    .tubemap {
        overflow: hidden;
        scale: 1.75;
        transform-origin: top left;
        margin: 0;
        width: auto;
